import FastClick from 'fastclick';

var u = navigator.userAgent;
// var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //androidÁªàÁ´Ø
var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //iosÁªàÁ´Ø

// ËøôÊ†∑ÁöÑÂ§ÑÁêÜÊòØ‰∏∫‰∫ÜÈò≤Ê≠¢Âú®Âçé‰∏∫P20Ëá™Â∏¶ÊµèËßàÂô®‰∏ãÔºåinputÊ°ÜÂî§ÈÜíÈîÆÁõòÂç°È°øÔºåÂøÖÈ°ªÈïøÊåâÊâçËÉΩÂî§ÈÜí
FastClick.attach(document.body);

if (isiOS) {
    /* eslint-disable */
    FastClick.prototype.focus = function(targetElement) {
        var length;
        //ÂÖºÂÆπÂ§ÑÁêÜ:Âú®iOS7‰∏≠ÔºåÊúâ‰∏Ä‰∫õÂÖÉÁ¥†ÔºàÂ¶Çdate„ÄÅdatetime„ÄÅmonthÁ≠âÔºâÂú®setSelectionRange‰ºöÂá∫Áé∞TypeError
        //ËøôÊòØÂõ†‰∏∫Ëøô‰∫õÂÖÉÁ¥†Âπ∂Ê≤°ÊúâselectionStartÂíåselectionEndÁöÑÊï¥ÂûãÊï∞Â≠óÂ±ûÊÄßÔºåÊâÄ‰ª•‰∏ÄÊó¶ÂºïÁî®Â∞±‰ºöÊä•ÈîôÔºåÂõ†Ê≠§ÊéíÈô§Ëøô‰∫õÂ±ûÊÄßÊâç‰ΩøÁî®setSelectionRangeÊñπÊ≥ï

        if (
            deviceIsIOS &&
            targetElement.setSelectionRange &&
            targetElement.type.indexOf('date') !== 0 &&
            targetElement.type !== 'time' &&
            targetElement.type !== 'month' &&
            targetElement.type !== 'email'
        ) {
            length = targetElement.value.length;
            targetElement.setSelectionRange(length, length);
            /*‰øÆÂ§çbug ios 11.3‰∏çÂºπÂá∫ÈîÆÁõòÔºåËøôÈáåÂä†‰∏äËÅöÁÑ¶‰ª£Á†ÅÔºåËÆ©ÂÖ∂Âº∫Âà∂ËÅöÁÑ¶ÂºπÂá∫ÈîÆÁõò*/
            targetElement.focus();
        } else {
            targetElement.focus();
        }
    };
    /* eslint-disable */
}
