/* eslint-disable*/
import axios from "axios";
import qs from "qs";

const interceptor = {
    requestInterceptor: null, //ËØ∑Ê±ÇÊã¶Êà™Âô®
    interceptorSuccess: null, //ÂìçÂ∫îÊàêÂäüÊã¶Êà™Âô®
    interceptError: null // ÂìçÂ∫îÂ§±Ë¥•Êã¶Êà™Âô®
};
const emptyFn = function() {};
let interceptInstance = null;
var defaultConfig = {
    isQs: false, // Êé•Âè£ÂèÇÊï∞ÊòØÂê¶ÈúÄË¶ÅqsÂ§ÑÁêÜ
    cache: false, // Êé•Âè£ÊòØÂê¶ÁºìÂ≠ò
    timeout: 6000, //Êé•Âè£ËØ∑Ê±ÇÊó∂Èó¥
    headers: {} // ÈªòËÆ§ËØ∑Ê±ÇÂ§¥
};
let timeout = 6000; //Êé•Âè£Ë∂ÖÊó∂Êó∂Èó¥
var headers = {}; // ÈªòËÆ§ËØ∑Ê±ÇÂ§¥
/**
 * @function _addInterceptors Ê∑ªÂä†Êã¶Êà™ÊñπÊ≥ï
 * @param  {object} options {Êé•Âè£ËØ∑Ê±ÇÂèÇÊï∞}
 * @return {Object} {Êé•Âè£ËØ∑Ê±ÇÊàñÂìçÂ∫îÁªìÊûú}
 */
const _addInterceptors = function(options) {
    interceptInstance = axios.create(); //ÂàõÂª∫axiosÂÆû‰æã
    // Ê∑ªÂä†ËØ∑Ê±ÇÊã¶Êà™Âô®
    interceptInstance.interceptors.request.use(
        config => {
            if (_interceptorCore("requestInterceptor", options, config)) {
                return config;
            }
            return config;
        },
        function(error) {
            return Promise.reject(error);
        }
    );
    // Ê∑ªÂä†ÂìçÂ∫îÊã¶Êà™Âô®
    interceptInstance.interceptors.response.use(
        response => {
            const result = response.data || response;
            if (_interceptorCore("interceptorSuccess", options, result)) {
                return;
            }

            return response;
        },
        thrown => {
            return Promise.reject(thrown);
        }
    );
    return interceptInstance;
};
/**
 * @function _interceptorCore Êã¶Êà™Âô®Â§ÑÁêÜÂáΩÊï∞
 * @param  {String} key      {Êã¶Êà™Á±ªÂûã}
 * @param  {Object} options  {Êé•Âè£ËØ∑Ê±ÇÂèÇÊï∞}
 * @param  {Object} response {Êé•Âè£ÂìçÂ∫îÁªìÊûú}
 * @return {Boolean} {ËøîÂõûÊòØÂê¶ÊâßË°åÊã¶Êà™ÂêéÈÄªËæëÁä∂ÊÄÅ}
 */
const _interceptorCore = function(key, options, response) {
    // Êé•Âè£ÊòØÂê¶Â≠òÂú®Â±ÄÈÉ®Êã¶Êà™ÊñπÊ≥ï
    if (options.hasOwnProperty(key) && typeof options[key] === "function") {
        if (options[key](response)) {
            return true;
        }
        return false;
    }
    // Êé•Âè£ÊòØÂê¶Â≠òÂú®ÂÖ®Â±ÄÊã¶Êà™ÊñπÊ≥ï
    if (
        interceptor.hasOwnProperty(key) &&
        typeof interceptor[key] === "function" &&
        interceptor[key](response)
    ) {
        return true;
    }
    return false;
};
/**
 * @function _sendCore ÂèëÈÄÅÊé•Âè£ËØ∑Ê±ÇÊ†∏ÂøÉÈÄªËæë
 * @param  {function} instance {axiosÂÆû‰æãÊñπÊ≥ï}
 * @param  {Object} options  {Êé•Âè£ËØ∑Ê±ÇÂèÇÊï∞}
 * @return {Object} {Êé•Âè£ÂìçÂ∫îÁªìÊûú}
 */
const _sendCore = function(instance, options) {
    const CancelToken = axios.CancelToken;
    const _url = options.url;
    const _headers = setHeaders(options.headers);
    const _data = options.data || null;
    const _method = options.method || "get";
    const _beforeSend = options.beforeSend || emptyFn;
    const _complete = options.complete || emptyFn;
    const _abort = options.abort || emptyFn;
    const _cache = options.hasOwnProperty("cache")
        ? options.cache
        : defaultConfig.cache;
    const _isQs = options.hasOwnProperty("isQs")
        ? options.isQs
        : defaultConfig.isQs;

    if (options.url === "") return null;

    const method = _method.toLocaleLowerCase();
    const config = {
        method: method,
        url: _url,
        data: _data,
        cancelToken: new CancelToken(function executor(c) {
            _abort(c);
        }),
        timeout: options.timeout || defaultConfig.timeout
    };

    const time = {
        _: +new Date()
    };

    if (method === "post" && config.data && isObject(config.data) && _isQs) {
        config.data = qs.stringify(config.data);
    }
    if (method === "get") {
        config.params = config.data;
        if (_cache) {
            config.data = config.data ? Object.assign(config.data, time) : time;
        }
        delete config.data;
    }
    if (_headers) {
        config.headers = _headers;
    }

    _beforeSend();
    return new Promise(function(resolve, reject) {
        instance
            .request(config)
            .then(function(response) {
                _complete();
                if (response) {
                    if (response.data) {
                        resolve(response.data);
                    } else {
                        reject(response);
                    }
                    reject(response);
                }
            })
            .catch(function(thrown) {
                _complete();
                const result = getResponseErrorType(thrown);

                if (result) {
                    if (_interceptorCore("interceptError", options, result)) {
                        return;
                    }
                    reject(result);
                }
                if (instance.isCancel && instance.isCancel(thrown)) {
                } else {
                    reject(thrown);
                }
            });
    });
};
/**
 * @function Ajax Âçï‰∏™Êé•Âè£ËØ∑Ê±Ç
 * @param  {Object} options {Êé•Âè£ËØ∑Ê±ÇÂèÇÊï∞}
 * @return {Object} {Êé•Âè£ÂìçÂ∫îÁªìÊûú}
 */
const ajax = function(options) {
    const instance = _addInterceptors(options);
    return _sendCore(instance, options);
};
/**
 * @function All ÂêåÊ≠•Êé•Âè£ËØ∑Ê±Ç
 * @param  {Arrary} list {Êé•Âè£ÂàóË°®}
 * @param  {Function} back {Êé•Âè£ÂìçÂ∫îÂõûË∞ÉÂáΩÊï∞}
 *
 */
const all = function(list, back) {
    const _back = typeof back === "function" ? back : emptyFn();

    if (typeof list === "function") {
        list = [list];
    }
    axios
        .all(list)
        .then(
            axios.spread(function(acct, perms) {
                const paramsArray = [];

                for (var i = 0; i < arguments.length; i++) {
                    paramsArray[i] = arguments[i];
                }
                _back(Array.prototype.slice.call(arguments));
            })
        )
        .catch(function(thrown) {
            back(thrown);
        });
};
/**
 * @function getResponseErrorType Ëé∑ÂèñËá™ÂÆö‰πâÊé•Âè£ÂìçÂ∫îÂ§±Ë¥•Á±ªÂûã
 * @param  {Object} thrown {Êé•Âè£ÂìçÂ∫îÂ§±Ë¥•ÂÜÖÂÆπ}
 * @return {Object} {Êé•Âè£ÂìçÂ∫îÂ§±Ë¥•Êï∞ÊçÆ}
 */
const getResponseErrorType = function(thrown) {
    let obj = null;
    let timeoutObj = {
        Message: "timeout",
        Data: null,
        Code: 666
    };
    let networkError = {
        Message: "Network Error",
        Data: null,
        Code: 555
    };
    if (thrown && thrown.message && thrown.message.indexOf("timeout") > -1) {
        obj = timeoutObj;
    }
    if (
        thrown &&
        thrown.message &&
        thrown.message.indexOf("Network Error") > -1
    ) {
        obj = networkError;
    }
    obj = !!obj ? obj : thrown;
    return obj;
};
/**
 * @function isNoEmptyObject Âà§Êñ≠ÂØπË±°ÊòØÂê¶‰∏∫Á©∫
 * @param  {Object} obj {ÂèÇÊï∞}
 * @return {Boolean} { true ÂØπË±°‰∏∫Á©∫ falseÂØπË±°‰∏ç‰∏∫Á©∫}
 */
const isNoEmptyObject = function(obj) {
    for (const key in obj) {
        return false; // only for eslint
    }
    return true;
};
/**
 * @function isObject Âà§Êñ≠ÊòØÂê¶ÊòØÂØπË±°
 * @param  {Object} obj {ÂèÇÊï∞}
 * @return {Boolean} {true ÊòØÂØπË±° false ‰∏çÊòØÂØπË±°}
 */
const isObject = function(obj) {
    return Object.prototype.toString.call(obj) === "[object Object]";
};
/**
 * @function setDefaults ËÆæÁΩÆÈªòËÆ§Êï∞ÊçÆ
 * @param  {Object} obj {ÈªòËÆ§Êï∞ÊçÆ}
 *
 */
let setDefaults = function(obj) {
    if (isObject(obj) && !isNoEmptyObject(obj)) {
        for (var i in interceptor) {
            interceptor[i] = obj[i] || emptyFn;
        }
        for (var i in defaultConfig) {
            defaultConfig[i] = obj[i] || defaultConfig[i];
        }
    }
};
/**
 * @function setHeaders
 * @param  {Object} params {ËØ∑Ê±ÇÂèÇÊï∞}
 *
 */
var setHeaders = function(params) {
    if (params && isObject(params)) return params;
    if (params === null) return null;
    return defaultConfig.headers;
};

export default { ajax, all, setDefaults, axios };
